<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="countryTableData" :noDataText="countryTableNoDataText" :sst="true" search @search="filterCountryTable" @sort="orderCountryTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadCountryTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="countryTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ countryTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="countryTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="geoId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="geoName">
            {{ this.$t('COUNTRY_NAME') }}
          </vs-th>
          <vs-th sort-key="geoCodeNumeric">
            {{ this.$t('NUMERIC_CODE') }}
          </vs-th>
          <vs-th sort-key="geoCodeAlpha2">
            {{ this.$t('ALPHA2_CODE') }}
          </vs-th>
          <vs-th sort-key="geoCodeAlpha3">
            {{ this.$t('ALPHA3_CODE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].geoId">
              {{ data[index].geoId }}
            </vs-td>
            <vs-td :data="data[index].geoName">
              <span :class="disabledClass(data[index])">{{ data[index].geoName }}</span>
            </vs-td>
            <vs-td :data="data[index].geoCodeNumeric">
              {{ data[index].geoCodeNumeric }}
            </vs-td>
            <vs-td :data="data[index].geoCodeAlpha2">
              {{ data[index].geoCodeAlpha2 }}
            </vs-td>
            <vs-td :data="data[index].geoCodeAlpha3">
              {{ data[index].geoCodeAlpha3 }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="countryTableTotalPages" v-model="countryTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import CountryTableMixin from '@/mixins/nucleus/CountryTableMixin'

export default {
  name: 'country-popup',
  mixins: [PopupMixin, CountryTableMixin],
  mounted: function () {
    this.loadCountryTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
